<template>
    <div class="console" id="terminal"></div>
</template>
<script>
import Terminal from './Xterm.js'
import {Base64} from 'js-base64'
import 'xterm/lib/addons/fullscreen/fullscreen.css'
import * as fullscreen from 'xterm/lib/addons/fullscreen/fullscreen'
Terminal.applyAddon(fullscreen)

let bindTerminal = (term, websocket, bidirectional, bufferedTime) => {
  term.socket = websocket
  let messageBuffer = null
  let handleWebSocketMessage = function (ev) {
    if (bufferedTime && bufferedTime > 0) {
      if (messageBuffer) {
        messageBuffer += ev.data
      } else {
        messageBuffer = ev.data
        setTimeout(function () {
          term.write(messageBuffer)
        }, bufferedTime)
      }
    } else {
      term.write(ev.data)
    }
  }

  let handleTerminalData = function (data) {
    websocket.send(
      JSON.stringify({
        type: 'cmd',
        cmd: Base64.encode(data) // encode data as base64 format
      })
    )
  }

  websocket.onmessage = handleWebSocketMessage
  if (bidirectional) {
    term.on('data', handleTerminalData)
  }

  // send heartbeat package to avoid closing webSocket connection in some proxy environmental such as nginx.
  let heartBeatTimer = setInterval(function () {
    websocket.send(JSON.stringify({ type: 'heartbeat', data: '' }))
  }, 20 * 1000)

  websocket.addEventListener('close', function () {
    websocket.removeEventListener('message', handleWebSocketMessage)
    term.off('data', handleTerminalData)
    delete term.socket
    clearInterval(heartBeatTimer)
  })
}

export default {
  name: 'Console',
  props: {
    terminal: {
      type: Object,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      term: null,
      terminalSocket: null
    }
  },
  methods: {
    runRealTerminal () {
      console.log('webSocket is finished')
    },
    closeRealTerminal () {
      console.log('close')
    }
  },
  mounted () {
    console.log('pid : ' + this.terminal.pid + ' is on ready')
    let terminalContainer = document.getElementById('terminal')
    this.term = new Terminal({
      rows: 60,
      fontSize: 18,
      cursorBlink: true,
      cursorStyle: 'bar'
    })
    // Terminal Configurations
    this.term._initialized = true
    this.term.on('selection', function () {
      if (this.term.hasSelection()) {
        this.copy = this.term.getSelection()
        console.log(this.copy)
      } else {
        console.log('where are you?')
      }
    })
    this.term.attachCustomKeyEventHandler(function (ev) {
      // 粘贴 ctrl+v
      if (ev.keyCode === 86 && ev.ctrlKey) {
        this.terminalSocket.send(new TextEncoder().encode('\x00' + this.copy))
      }
    })
    this.term.open(terminalContainer)
    this.term.fit()
    // open websocket
    this.terminalSocket = new WebSocket('ws://16.186.79.97:8099/ssh/ws')
    this.terminalSocket.onopen = this.runRealTerminal
    this.terminalSocket.onclose = this.closeRealTerminal
    this.terminalSocket.onerror = function (evt) {
      console.log('error', evt)
    }
    bindTerminal(this.term, this.terminalSocket, true, -1)
    console.log('mounted is going on')
  },
  beforeDestroy () {
    this.terminalSocket.close()
    this.term.destroy()
  }
}
</script>



